import React from 'react'
import Navbar from '../components/Navbar'
import Sidebar from '../components/Sidebar'
import { useState } from "react";
import HeroSection from '../components/HeroSection';
import InfoSection from '../components/InfoSection';
import { aboutusSection, guardianshipSection, homestaySection, courseSection, newsSection, blogSection, workSection, contactSection, covidSection } from '../components/InfoSection/Data';
import Footer from "../components/Footer";

const Home = () => {

  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => {
    setIsOpen(!isOpen)
  }

  return (
    <>      
      <Sidebar isOpen ={isOpen} toggle={toggle}/>
      <Navbar toggle={toggle}/>
      <HeroSection />
      <InfoSection {...covidSection}/>
      <InfoSection {...aboutusSection}/>
      <InfoSection {...guardianshipSection}/>
      <InfoSection {...homestaySection}/>
      <InfoSection {...courseSection}/>
      {/* <InfoSection {...newsSection}/> */}
      <InfoSection {...blogSection}/>
      <InfoSection {...workSection}/>
      <InfoSection {...contactSection}/>

      <Footer />
    </>
  )
}

export default Home
